@charset "utf-8";

.todo_item{
  padding: 1rem;
  display: flex;
  align-items: center;
  line-height: 40px;
  cursor: pointer;
}
.todo_item:hover{background-color: #ccc;}
/* todo_item에 마우스가 있을 때만 .remove보이기 */
.todo_item:hover .remove{opacity: 1;}
/* todo_item사이에 윗 테두리 */
.todo_item + .todo_item{border-top: 1px solid #f1f3f5;}

/* 삭제버튼 서식 */
.remove{
  opacity: 0;
  font-weight: bold;
  color: #d30505;
  margin-right: 0.5rem;
}
.todo_text{
  flex: 1;
  word-break: break-all;
}
.checked{ 
  /* 해당 글 목록 클릭시 글자 서식이 흐리고 가운데 줄 표시 */
  text-decoration: line-through;
  color: #adb5bd;
}
.check_mark{
  font-size: 1.5rem;
  line-height: normal;
  color: #224ec5;
  font-weight: bold;
}